import React, { useState, useEffect, useRef } from 'react';
import { auth, storage, firestore } from '../firebase';
import { updatePassword } from 'firebase/auth';
import { isPasswordValid } from '../firebase';
import { doc, setDoc, getDoc, updateDoc, deleteField, collection, query, where, getDocs } from 'firebase/firestore';

function Settings() {
  const [user, setUser] = useState(null);
  const [oldPassword, setOldPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [passwordError, setPasswordError] = useState('');
  const [successMessage, setSuccessMessage] = useState('');
  const [customURL, setCustomURL] = useState('');

  const [isMobile, setIsMobile] = useState(false);

  const [customURLSuccessMessage, setCustomURLSuccessMessage] = useState('');
  const [customURLErrorMessage, setCustomURLErrorMessage] = useState('');

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768); // Adjust the breakpoint as needed
    };
  
    handleResize(); // Call it initially to set the initial state
  
    window.addEventListener('resize', handleResize);
  
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((authUser) => {
      if (authUser) {
        setUser(authUser);
        const splitName = authUser.displayName.split(' ');
        setFirstName(splitName[0] || '');
        setLastName(splitName[1] || '');
  
        // Fetch customURL from Firestore
        const userUID = authUser.uid;
        const userProfileRef = doc(firestore, 'userProfiles', userUID);
  
        getDoc(userProfileRef)
          .then((docSnapshot) => {
            if (docSnapshot.exists()) {
              const userData = docSnapshot.data();
              if (userData.customURL) {
                setCustomURL(userData.customURL);
                // Store customURL in local storage
                localStorage.setItem('customURL', userData.customURL);
              }
            }
          })
          .catch((error) => {
            console.error('Error fetching custom URL: ', error);
          });
      } else {
        setUser(null);
        setFirstName('');
        setLastName('');
      }
    });
  
    // Retrieve customURL from local storage on component mount
    const storedCustomURL = localStorage.getItem('customURL');
    if (storedCustomURL) {
      setCustomURL(storedCustomURL);
    }
  
    return () => {
      unsubscribe();
    };
  }, []);


  

const handlePasswordUpdate = () => {
  if (newPassword === '' && confirmPassword === '') {
    setPasswordError("Please type a new password.");
    return;
  }

  if (newPassword !== confirmPassword) {
    setPasswordError("New password and confirm password don't match.");
    return;
  }

  let passwordError = '';

  if (newPassword.length < 8) {
    passwordError = 'Password must be more than 7 characters.';
  } else if (!/[a-z]/.test(newPassword)) {
    passwordError = 'Password must contain at least one lowercase letter.';
  } else if (!/[A-Z]/.test(newPassword)) {
    passwordError = 'Password must contain at least one uppercase letter.';
  } else if (!/\d/.test(newPassword)) {
    passwordError = 'Password must contain at least one digit.';
  } else if (!/[@$!%*?&]/.test(newPassword)) {
    passwordError = 'Password must contain at least one special character.';
  }

  if (passwordError) {
    setPasswordError(passwordError);
    return;
  }

  const user = auth.currentUser;
  updatePassword(user, newPassword)
    .then(() => {
      setSuccessMessage('Password updated successfully');
      setOldPassword('');
      setNewPassword('');
      setConfirmPassword('');
      setPasswordError('');
    })
    .catch((error) => {
      alert('Error updating password: ' + error.message);
    });
};




const handleCustomURLUpdate = () => {
  if (customURL === '') {
    console.error('Custom URL is empty.');
    setCustomURLErrorMessage('Custom URL cannot be empty.');
    return;
  }

  // Convert customURL to lowercase
  const lowercaseCustomURL = customURL.toLowerCase();

  // Check if the custom URL is already in use
  const userProfilesRef = collection(firestore, 'userProfiles');
  const customURLQuery = query(userProfilesRef, where('customURL', '==', lowercaseCustomURL));

  getDocs(customURLQuery)
    .then((querySnapshot) => {
      if (querySnapshot.size > 0) {
        // Custom URL is already in use
        setCustomURLErrorMessage('Custom URL is already taken. Please choose another one.');
        setCustomURLSuccessMessage('');
      } else {
        // Custom URL is unique, update it in the database
        const userUID = user.uid;
        const userProfileRef = doc(firestore, 'userProfiles', userUID);

        updateDoc(userProfileRef, {
          customURL: lowercaseCustomURL, // Store in lowercase
        })
          .then(() => {
            console.log('Custom URL updated successfully');
            setCustomURLSuccessMessage('Custom URL updated successfully');
            setCustomURLErrorMessage('');
          })
          .catch((error) => {
            console.error('Error updating custom URL: ', error);
            setCustomURLErrorMessage('Error updating custom URL.');
            setCustomURLSuccessMessage('');
          });
      }
    })
    .catch((error) => {
      console.error('Error checking custom URL availability: ', error);
      setCustomURLErrorMessage('Error checking custom URL availability.');
      setCustomURLSuccessMessage('');
    });
};


const handleInputKeyDown = (event) => {
  if (event.key === 'Enter') {
    handleCustomURLUpdate();
  }
};


  return (
    <div className="profile-container">
      <div className="profile-intro-section">
        
      </div>

      <div className="profile-section">
        <div className="content">
          <div className="my-4">
            <h3>My Settings</h3>
            <hr />
          </div>
          
            <div className="row mb-4 gx-4">
              <div className="col-xl-12 col-12">
                <div className="">
                  <div className="card-body card-profile">
          
                    <div className="row g-3">
                      
                      <h4 className="mb-4">Custom URL</h4>
                      <div className={isMobile ? '' : 'input-group'}>
  <span className="customurl">https://www.tioassist.io/user/</span>
  <input
    type="text"
    className="form-control form-control-lg"
    placeholder="yourHTML"
    value={customURL}
    onChange={(e) => setCustomURL(e.target.value)}
    onKeyDown={handleInputKeyDown} // Submit on Enter
  />
</div>

      </div>
      <div className="mt-3">
      {customURLSuccessMessage && (
          <p style={{ color: 'green', margin: '10px 0' }}>
            {customURLSuccessMessage}
          </p>
        )}
        {customURLErrorMessage && (
          <p style={{ color: 'red', margin: '10px 0' }}>
            {customURLErrorMessage}
          </p>
        )}
        <button
          type="button"
          className="btn btn-primary"
          onClick={handleCustomURLUpdate}
        

        >
          Update Custom URL
        </button>
        </div>
      
          
                    {user && user.providerData[0].providerId === 'google.com' ? (
                  null // Do not render password-related form for Google Sign-In users
                ) : (
                    <form className="file-upload" autoComplete="new-password">
                    <div className="py-5 rounded mt-4">
                      <div className="row g-3">
                        <h4 className="mb-4">Password</h4>
                        <div className="col-md-4">
                          <label htmlFor="exampleInputPassword1" className="form-label">
                            Old password *
                          </label>
                          <input
                            type="password"
                            className="form-control form-control-lg"
                            id="exampleInputPassword1"
                            value={oldPassword}
                            onChange={(e) => setOldPassword(e.target.value)}
                          />
                        </div>
                        <div className="col-md-4">
                          <label htmlFor="exampleInputPassword2" className="form-label">
                            New password *
                          </label>
                          <input
                            type="password"
                            className="form-control form-control-lg"
                            id="exampleInputPassword2"
                            value={newPassword}
                            onChange={(e) => setNewPassword(e.target.value)}
                          />
                        </div>
                        <div className="col-md-8">
                          <label htmlFor="exampleInputPassword3" className="form-label">
                            Confirm Password *
                          </label>
                          <input
                            type="password"
                            className="form-control form-control-lg"
                            id="exampleInputPassword3"
                            value={confirmPassword}
                            onChange={(e) => setConfirmPassword(e.target.value)}
                          />
                        {successMessage && (
        <div className="text-success">{successMessage}</div>
      )}
      {passwordError && (
        <div className="text-danger">{passwordError}</div>
      )}
                        </div>
                      </div>
                      <div className="mt-3">
                        <button
                          type="button"
                          className="btn btn-primary"
                          onClick={handlePasswordUpdate}
                        >
                          Update Password
                        </button>
                      </div>
                    </div>
                    </form>
                    )}
                  </div>
                </div>
              </div>
              
            </div>
            <div className="col-xl-12 col-12">
                <div className="bg-white shadow-sm mb-4 card border-light">
                  <div className="card-body card-profile">
          
                    <div className="row g-3">
                      
                      <h4 className="mb-4">Delete Profile</h4>
                      <div className='mt-3'>
<button type="button" className="btn btn-danger">
Delete Profile
</button>
</div>
        </div>
     </div>
     </div>
     </div>
     </div>
      </div>
    </div>
  );
}

export default Settings;


<div className='mt-3'>
<button type="button" className="btn btn-danger">
Delete profile
</button>
</div>